

body{
    margin: 0;
    background-color: #E5BE9E;
    font-family: sans-serif;
}

.todo-container{
    width: 350px;
    margin: auto;
    margin-top: 30px;
    text-align: center;
}
.input-section{
    background-color: #E9B887;
    padding: 10px;
    border-radius: 10px;
    box-shadow: 0 6px #c7915a;
}
.input-section input{
    border-radius: 5px;
    border: 1px solid rgba(235, 128, 78, 0);
    width: 100%;
    padding: 10px;
    box-sizing: border-box;
    box-shadow: 0 3px#c0905e85;
    outline: none;
    background-color: #f1c497;
    color: #7e5123;
}

input::placeholder{
    color: #c0905e;
}

ul{
    text-align: left;
    padding: 0%;
}

ul li{
    list-style-type: none;
    background-color: #f1c497;
    padding: 15px;
    border-radius: 5px;
    margin-bottom: 10px;
    box-shadow: 0 6px #E7B37E;
    color: #c0905e;
    display: flex;
}

ul li i{
    text-align: right;
    flex: 1;
    cursor: pointer;
}

h1{
    margin: 0;
    margin-bottom: 15px;
    color: #ffd9b4;
}